// src/components/Home.js
import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import { Clock } from 'lucide-react';
import { loadArticles } from '../utils/loadArticles';
import { motion } from 'framer-motion';
import { authors } from '../data/authors'; // Import authors data

const ReadTime = ({ minutes }) => (
  <div className="flex items-center text-gray-500 text-sm">
    <Clock size={14} className="mr-1" />
    <span>{minutes} MIN READ</span>
  </div>
);

const Home = () => {
  const [articles, setArticles] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    const fetchArticles = async () => {
      try {
        const loadedArticles = await loadArticles();
        setArticles(loadedArticles);
      } catch (err) {
        setError(err.message);
      } finally {
        setLoading(false);
      }
    };

    fetchArticles();
  }, []);

  if (loading) return <div>Loading articles...</div>;
  if (error) return <div className="text-red-500">Error: {error}</div>;

  const mainArticles = articles.slice(0, 2);
  const secondaryArticles = articles.slice(2, 5);
  const listArticles = articles.slice(5, 10);
  const remainingArticles = articles.slice(10);

  return (
    <motion.div
      className="max-w-6xl mx-auto px-4"
      initial={{ y: 20, opacity: 0 }}
      animate={{ y: 0, opacity: 1 }}
      transition={{ duration: 0.5 }}
    >
      {/* Main Stories Section */}
      <motion.div
        className="grid grid-cols-1 md:grid-cols-2 gap-8 mb-12 border-b pb-12"
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        transition={{ delay: 0.2, duration: 0.5 }}
      >
        {mainArticles.map((article, index) => (
          <Link
            key={index}
            to={`/article/${article.folder.split('/').pop()}`}
            className="block hover:opacity-80 transition-opacity"
          >
            <article className="flex flex-col h-full">
              <div className="flex-1">
                <h1 className="font-serif text-3xl md:text-4xl font-bold leading-tight mb-3">
                  {article.title}
                </h1>
                <p className="text-gray-700 text-lg mb-4 font-serif leading-snug">
                  {article.subtitle}
                </p>
                <div className="flex items-center justify-between mb-4">
                  <span className="text-sm text-gray-500">
                    By {article.authors.map((name, idx) => {
                      const author = authors.find(a => a.name === name);
                      return author ? (
                        <Link 
                          key={idx}
                          to={`/author/${author.username}`} 
                          className="underline"
                        >
                          {name}{idx < article.authors.length -1 ? ', ' : ''}
                        </Link>
                      ) : (
                        <span key={idx}>{name}{idx < article.authors.length -1 ? ', ' : ''}</span>
                      );
                    })}
                  </span>
                  <ReadTime minutes={article.readingTime || 6} />
                </div>
              </div>
              {article.topImage && (
                <div className="mt-4">
                  <img
                    src={`/articles/${article.folder}/${article.topImage}`}
                    alt={article.title}
                    className="w-full h-64 object-cover rounded"
                  />
                </div>
              )}
            </article>
          </Link>
        ))}
      </motion.div>

      {/* Secondary Stories */}
      <motion.div
        className="grid grid-cols-1 md:grid-cols-3 gap-6 mb-12 border-b pb-12"
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        transition={{ delay: 0.4, duration: 0.5 }}
      >
        {secondaryArticles.map((article, index) => (
          <Link
            key={index}
            to={`/article/${article.folder.split('/').pop()}`}
            className="block hover:opacity-80 transition-opacity"
          >
            <article className="flex flex-col h-full">
              {article.topImage && (
                <div className="mb-3">
                  <img
                    src={`/articles/${article.folder}/${article.topImage}`}
                    alt={article.title}
                    className="w-full h-40 object-cover rounded"
                  />
                </div>
              )}
              <h2 className="font-serif text-2xl font-bold mb-2 leading-tight">
                {article.title}
              </h2>
              <p className="text-gray-600 text-base mb-2">
                {article.subtitle}
              </p>
              <div className="mt-auto flex items-center justify-between">
                <span className="text-sm text-gray-500">
                  By {article.authors.map((name, idx) => {
                    const author = authors.find(a => a.name === name);
                    return author ? (
                      <Link 
                        key={idx}
                        to={`/author/${author.username}`} 
                        className="hover:underline"
                      >
                        {name}{idx < article.authors.length -1 ? ', ' : ''}
                      </Link>
                    ) : (
                      <span key={idx}>{name}{idx < article.authors.length -1 ? ', ' : ''}</span>
                    );
                  })}
                </span>
                <ReadTime minutes={article.readingTime || 4} />
              </div>
            </article>
          </Link>
        ))}
      </motion.div>

      {/* List View Articles */}
      <motion.div
        className="mb-12 border-b pb-12"
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        transition={{ delay: 0.6, duration: 0.5 }}
      >
        {listArticles.map((article, index) => (
          <Link
            key={index}
            to={`/article/${article.folder.split('/').pop()}`}
            className="block hover:opacity-80 transition-opacity"
          >
            <article className="mb-6 pb-6 border-b last:border-b-0 last:pb-0">
              <div className="flex flex-col md:flex-row justify-between items-start">
                <div className="flex-grow pr-4">
                  <h3 className="font-serif text-lg font-bold mb-2">
                    {article.title}
                  </h3>
                  <div className="flex items-center justify-between">
                    <span className="text-sm text-gray-500">
                      By {article.authors.map((name, idx) => {
                        const author = authors.find(a => a.name === name);
                        return author ? (
                          <Link 
                            key={idx}
                            to={`/author/${author.username}`} 
                            className="hover:underline"
                          >
                            {name}{idx < article.authors.length -1 ? ', ' : ''}
                          </Link>
                        ) : (
                          <span key={idx}>{name}{idx < article.authors.length -1 ? ', ' : ''}</span>
                        );
                      })}
                    </span>
                    <ReadTime minutes={article.readingTime || 3} />
                  </div>
                </div>
                {article.topImage && (
                  <div className="mt-2 md:mt-0">
                    <img
                      src={`/articles/${article.folder}/${article.topImage}`}
                      alt={article.title}
                      className="w-24 h-24 object-cover rounded"
                    />
                  </div>
                )}
              </div>
            </article>
          </Link>
        ))}
      </motion.div>

      {/* Grid View for Remaining Articles */}
      <motion.div
        className="grid grid-cols-1 md:grid-cols-4 gap-6"
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        transition={{ delay: 0.8, duration: 0.5 }}
      >
        {remainingArticles.map((article, index) => (
          <Link
            key={index}
            to={`/article/${article.folder.split('/').pop()}`}
            className="block hover:opacity-80 transition-opacity"
          >
            <article className="mb-6">
              <h4 className="font-serif text-base font-bold mb-2">
                {article.title}
              </h4>
              <div className="text-xs text-gray-500">
                By {article.authors.map((name, idx) => {
                  const author = authors.find(a => a.name === name);
                  return author ? (
                    <Link 
                      key={idx}
                      to={`/author/${author.username}`} 
                      className="hover:underline"
                    >
                      {name}{idx < article.authors.length -1 ? ', ' : ''}
                    </Link>
                  ) : (
                    <span key={idx}>{name}{idx < article.authors.length -1 ? ', ' : ''}</span>
                  );
                })}
              </div>
            </article>
          </Link>
        ))}
      </motion.div>
    </motion.div>
  );
};

export default Home;