import React, { useEffect, useState } from 'react';
import { useParams, Link } from 'react-router-dom';
import { loadArticles } from '../utils/loadArticles';
import { motion } from 'framer-motion';
import { Clock } from 'lucide-react';

const ReadTime = ({ minutes }) => (
  <div className="flex items-center text-gray-500 text-sm">
    <Clock size={14} className="mr-1" />
    <span>{minutes} MIN READ</span>
  </div>
);

const Category = () => {
  const { category, type } = useParams(); // Updated to include type
  const [articles, setArticles] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    const fetchCategoryArticles = async () => {
      try {
        const allArticles = await loadArticles();
        let filteredArticles;

        if (category) {
          // Decode and normalize the category parameter
          const normalizedCategory = decodeURIComponent(category).toLowerCase();
          const normalizedType = decodeURIComponent(category).toLowerCase(); 
          console.log('normalizedType:', normalizedType);
          // Filter by topics with case-insensitive matching
          filteredArticles = allArticles.filter(article => 
            article.topics.some(topic => topic.toLowerCase() === normalizedCategory || decodeURIComponent(article.type).toLowerCase().replace(/ /g, '-') === normalizedType) || article.reach.toLowerCase().replace(/ /g, '-') === normalizedType
          );
        } else if (type) {
          // Decode and normalize the type parameter
          const normalizedType = decodeURIComponent(type).toLowerCase();
          
          // Filter by type or reach with case-insensitive matching
          filteredArticles = allArticles.filter(article => 
            article.type.toLowerCase() === normalizedType || 
            article.reach.toLowerCase() === normalizedType
          );
        } else {
          filteredArticles = [];
        }

        setArticles(filteredArticles);
      } catch (err) {
        setError(err.message);
      } finally {
        setLoading(false);
      }
    };

    fetchCategoryArticles();
  }, [category, type]); // Updated dependencies

  if (loading) return <div>Loading articles...</div>;
  if (error) return <div className="text-red-500">Error: {error}</div>;
  if (articles.length === 0) return <div>No articles found under this category.</div>;

  let betterCategory = category;

  if (betterCategory === 'op-ed') {
      betterCategory = 'Op-Ed';
  } else if (!/^[A-Z]/.test(betterCategory) || betterCategory.includes('-')) {
    betterCategory = betterCategory
        .replace(/-/g, ' ') // Replace hyphens with spaces
        .split(' ') // Split into words
        .map((word, index) => {
            const lowercaseWords = ['and', 'or', 'of', 'in', 'on', 'at', 'to', 'a', 'an', 'the'];
            // Capitalize the first word or non-lowercase exceptions
            if (index === 0 || !lowercaseWords.includes(word.toLowerCase())) {
                return word.charAt(0).toUpperCase() + word.slice(1).toLowerCase();
            }
            return word.toLowerCase(); // Lowercase for exceptions
        })
        .join(' '); // Join back into a single string
  }
  return (
    <motion.div
      className="max-w-6xl mx-auto px-4"
      initial={{ y: 20, opacity: 0 }}
      animate={{ y: 0, opacity: 1 }}
      transition={{ duration: 0.5 }}
    >
      <h1 className="text-4xl font-bold mb-8">
      {decodeURIComponent(betterCategory)}
      </h1>
      <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
        {articles.map((article, index) => (
          <Link
            key={index}
            to={`/article/${article.folder.split('/').pop()}`}
            className="block hover:opacity-80 transition-opacity"
          >
            <article className="flex flex-col h-full">
              <div className="flex-1">
                <h2 className="font-serif text-2xl font-bold leading-tight mb-2">
                  {article.title}
                </h2>
                <p className="text-gray-600 text-base mb-4">
                  {article.subtitle}
                </p>
                <div className="flex items-center justify-between">
                  <span className="text-sm text-gray-500">
                    By {article.authors.join(', ')}
                  </span>
                  <ReadTime minutes={article.readingTime || 5} />
                </div>
              </div>
              {article.topImage && (
                <div className="mt-4">
                  <img
                    src={`/articles/${article.folder}/${article.topImage}`}
                    alt={article.title}
                    className="w-full h-48 object-cover rounded"
                  />
                </div>
              )}
            </article>
          </Link>
        ))}
      </div>
    </motion.div>
  );
};

export default Category;