import React, { useEffect, useState } from 'react';
import { useParams, Link } from 'react-router-dom';
import { authors } from '../data/authors';
import { loadArticles } from '../utils/loadArticles';
import { motion } from 'framer-motion';
import noperson from '../noperson.jpg'; // Import the default image

const Author = () => {
  const { username } = useParams();
  const [author, setAuthor] = useState(null);
  const [articles, setArticles] = useState([]);
  const [error, setError] = useState(null);

  useEffect(() => {
    const fetchAuthorData = async () => {
      const foundAuthor = authors.find(a => a.username === username);
      if (!foundAuthor) {
        setError('Author not found');
        return;
      }
      setAuthor(foundAuthor);

      try {
        const allArticles = await loadArticles();
        const authorArticles = allArticles.filter(article =>
          article.authors.includes(foundAuthor.name)
        );
        setArticles(authorArticles);
      } catch (err) {
        setError('Failed to load articles');
      }
    };

    fetchAuthorData();
  }, [username]);

  if (error) return <div className="text-red-500">{error}</div>;
  if (!author) return <div>Loading...</div>;

  return (
    <motion.div
      className="max-w-4xl mx-auto p-4"
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      transition={{ duration: 0.5 }}
    >
      <div className="flex flex-col items-center">
        <img
          src={author.image}
          alt={author.name}
          className="w-48 h-48 rounded-full mb-4"
          onError={(e) => { e.target.src = noperson; }} // Fallback to default image
        />
        <h1 className="text-3xl font-bold">{author.name}</h1>
        <h2 className="text-xl text-gray-600 mb-4">{author.bio}</h2>
        <p className="text-gray-700 mb-2">
          <strong>Email:</strong> <a href={`mailto:${author.email}`} className="text-gray-700 underline">{author.email}</a>
        </p>
        <p className="text-gray-700 mb-4">
          <strong>LinkedIn:</strong> <a href={author.linkedin} className="text-gray-700 underline" target="_blank" rel="noopener noreferrer">View Profile</a>
        </p>
      </div>

      <div className="mt-8">
        <h3 className="text-2xl font-semibold mb-4">Articles by {author.name}</h3>
        {articles.length === 0 ? (
          <p>No articles found.</p>
        ) : (
          <ul className="space-y-4">
            {articles.map((article, index) => (
              <motion.div
                whileHover={{ scale: 1.01 }}
                className="outline outline-gray-300"
              >
                <Link to={`/article/${article.folder.split('/').pop()}`} className="text-xl text-gray-700">
                  <li key={index} className="p-4 border rounded shadow-md">
                    {article.title}
                    <p className="text-gray-600">{article.subtitle}</p>
                    {/* Additional metadata */}
                    <p className="text-sm text-gray-500">Date: {article.date}</p>
                    <p className="text-sm text-gray-500">Topics: {article.topics.join(', ')}</p>
                    <p className="text-sm text-gray-500">{article.reach}  {article.type}</p>
                  </li>
                </Link>
              </motion.div>
            ))}
          </ul>
        )}
      </div>
    </motion.div>
  );
};

export default Author;