import React, { useEffect, useState, useRef } from "react";
import { useParams, Link } from "react-router-dom";
import { Clock, Share2, Bookmark, TwitterIcon, Share } from 'lucide-react';
import { loadArticle } from "../utils/loadArticles";
import ReactMarkdown from "react-markdown";
import remarkGfm from 'remark-gfm';
import rehypeRaw from 'rehype-raw';
import { motion } from 'framer-motion';
import { authors } from '../data/authors';
import { slugify } from '../utils/slugify'; // Import slugify utility

const SocialShareButton = ({ icon: Icon, label, onClick }) => (
  <button 
    onClick={onClick}
    className="p-2 rounded-full bg-gray-100 hover:bg-gray-200 transition-colors duration-200"
    aria-label={label}
  >
    <Icon className="w-5 h-5" />
  </button>
);

const Article = () => {
  const { folder } = useParams();
  const [article, setArticle] = useState(null);
  const [error, setError] = useState(null);
  const [readingProgress, setReadingProgress] = useState(0);
  const articleRef = useRef(null);

  useEffect(() => {
    const fetchArticle = async () => {
      try {
        const loadedArticle = await loadArticle(folder);
        setArticle(loadedArticle);
      } catch (err) {
        setError(err.message);
        console.error('Error loading article:', err);
      }
    };
    fetchArticle();
  }, [folder]);

  useEffect(() => {
    const calculateReadingProgress = () => {
      if (!articleRef.current) return;
      
      const articleBox = articleRef.current.getBoundingClientRect();
      const totalHeight = articleBox.height;
      const windowHeight = window.innerHeight;
      const scrolled = window.scrollY;
      const progress = (scrolled / (totalHeight - windowHeight)) * 100;
      setReadingProgress(Math.min(100, Math.max(0, progress)));
    };

    window.addEventListener('scroll', calculateReadingProgress);
    return () => window.removeEventListener('scroll', calculateReadingProgress);
  }, []);

  if (error) return <div className="text-red-500">Error: {error}</div>;
  if (!article) return <div>Loading...</div>;

  const shareArticle = (platform) => {
    const url = window.location.href;
    const title = article.title;
    
    const urls = {
      twitter: `https://twitter.com/intent/tweet?text=${encodeURIComponent(title)}&url=${encodeURIComponent(url)}`,
      facebook: `https://www.facebook.com/sharer/sharer.php?u=${encodeURIComponent(url)}`,
      general: () => {
        if (navigator.share) {
          navigator.share({
            title: title,
            url: url
          }).catch(console.error);
        }
      }
    };

    if (platform === 'general') {
      urls.general();
    } else {
      window.open(urls[platform], '_blank', 'width=600,height=400');
    }
  };

  return (
    <>
      <div 
        className="fixed top-0 left-0 w-full h-1 bg-gray-200 z-50 transition-transform duration-300"
        style={{ transform: `translateX(${readingProgress}%)` }}
      >
        <div className="h-full bg-black transition-transform duration-300" />
      </div>

      <motion.div
        className="relative max-w-7xl mx-auto px-4 py-8"
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        transition={{ duration: 0.5 }}
        ref={articleRef}
      >
        <div className="hidden lg:flex fixed left-16 top-1/4 flex-col space-y-4 transition-opacity duration-300 opacity-0 animate-fadeIn">
          <SocialShareButton icon={TwitterIcon} label="Share on Twitter" onClick={() => shareArticle('twitter')} />
          <SocialShareButton icon={Share} label="Share" onClick={() => shareArticle('general')} />
        </div>

        <div className="lg:hidden fixed bottom-0 left-0 right-0 bg-white border-t p-4 flex justify-center space-x-4 transition-transform duration-300 transform translate-y-4 opacity-0 animate-fadeIn">
          <SocialShareButton icon={TwitterIcon} label="Share on Twitter" onClick={() => shareArticle('twitter')} />
          <SocialShareButton icon={Share} label="Share" onClick={() => shareArticle('general')} />
        </div>

        <div className="grid grid-cols-12 gap-8">
          <article className="col-span-12 lg:col-span-8">
            <header className="mb-12">
              <div className="flex flex-wrap mb-6">
                {article.topics.map((topic, index) => (
                  <Link 
                    key={index}
                    to={`/topic/${topic}`}
                    className="inline-block px-2 py-1 bg-gray-100 text-xs font-medium rounded-full mr-2 mb-2"
                  >
                    {topic}
                  </Link>
                ))}
              </div>
              
              <h1 className="font-serif text-5xl mb-4 leading-tight">
                {article.title}
              </h1>
              {article.subtitle && (
                <p className="text-2xl text-gray-600 mb-8 leading-relaxed">{article.subtitle}</p>
              )}

              {article.type && (
                <Link 
                  to={`/category/${article.typeSlug}`} // Use slugified type
                  className="inline-block px-3 py-1 bg-gray-100 text-sm font-medium rounded-full mb-4"
                >
                  {article.type}
                </Link>
              )}

              <div className="flex items-center justify-between mb-8 pb-6 border-b">
                <div className="flex items-center">
                  {article.authorImage && (
                    <img 
                      src={`${article.folder}/${article.authorImage}`}
                      alt={article.authors[0]}
                      className="w-12 h-12 rounded-full mr-4"
                    />
                  )}
                  <div>
                    <p className="font-semibold text-lg mb-1">
                      {Array.isArray(article.authors) ? article.authors.map((authorName, idx) => {
                        const author = authors.find(a => a.name === authorName);
                        return author ? (
                          <Link 
                            key={idx}
                            to={`/author/${author.username}`} 
                            className="underline"
                          >
                            {authorName}{idx < article.authors.length -1 ? ', ' : ''}
                          </Link>
                        ) : (
                          <span key={idx}>{authorName}{idx < article.authors.length -1 ? ', ' : ''}</span>
                        );
                      }) : article.authors}
                    </p>
                    <div className="flex items-center text-gray-500 text-sm">
                      <span>{article.date}</span>
                      <span className="mx-2"></span>
                      <span>{article.reach}</span>
                      <span className="mx-2"></span>
                      <Clock size={14} className="mr-1" />
                      <span>{article.readingTime || '6'} min read</span>
                    </div>
                  </div>
                </div>
              </div>

              {article.heroImage && (
                <figure className="mb-8">
                  <img
                    src={`${article.folder}/${article.heroImage}`}
                    alt={article.imageCaption || article.title}
                    className="w-full rounded-lg"
                  />
                  {article.imageCaption && (
                    <figcaption className="text-sm text-gray-500 mt-2 italic">
                      {article.imageCaption}
                    </figcaption>
                  )}
                </figure>
              )}
            </header>

            <div className="prose prose-lg max-w-none">
              <ReactMarkdown
                remarkPlugins={[remarkGfm]}
                rehypePlugins={[rehypeRaw]}
                components={{
                  p: ({node, ...props}) => <p className="text-lg leading-relaxed mb-6" {...props} />,
                  h2: ({node, ...props}) => <h2 className="font-serif text-2xl mt-8 mb-4" {...props} />,
                  img: ({node, src, alt, ...props}) => (
                    <div className="my-8">
                      <img 
                        src={`${article.folder}/${src}`} 
                        alt={alt} 
                        className="w-full" 
                        {...props} 
                      />
                      {alt && <p className="text-sm text-gray-500 italic mt-2">{alt}</p>}
                    </div>
                  ),
                  blockquote: ({node, ...props}) => (
                    <blockquote className="border-l-4 border-gray-800 pl-4 italic my-8" {...props} />
                  ),
                }}
              >
                {article.content}
              </ReactMarkdown>
            </div>
          </article>

          <aside className="hidden lg:block col-span-4">
            <div className="sticky top-24 border-l pl-8">
              <h2 className="font-serif text-lg mb-6">More in {article.type}</h2>
            </div>
          </aside>
        </div>
      </motion.div>
    </>
  );
};

export default Article;