import { authors } from '../data/authors'; // Import authors data
import { slugify } from './slugify'; // Import slugify utility

export const loadArticles = async () => {
  try {
    // Import all meta.json files
    const metaContext = require.context('../articles', true, /meta\.json$/);
    
    // Get all the possible paths
    const paths = metaContext.keys();
    
    // Map paths to articles data
    const articles = paths.map(path => {
      // Load the meta data
      const meta = metaContext(path);
      
      // Extract folder name
      const folder = path.split('/')[1];
      
      // Map author names to usernames
      const authorUsernames = meta.authors.map(name => {
        const author = authors.find(a => a.name === name);
        return author ? author.username : null;
      }).filter(username => username !== null);
      
      // Create slugs for type and reach
      const typeSlug = slugify(meta.type);
      const reachSlug = slugify(meta.reach);
      
      return {
        ...meta,
        folder,
        path: `/articles/${folder}`,
        authorUsernames, // Add usernames field
        typeSlug,        // Add slug for type
        reachSlug,       // Add slug for reach
      };
    });

    return articles
      .sort((a, b) => new Date(b.date) - new Date(a.date))
      .map(article => ({
        ...article,
        topics: article.topics, // Use topics array
        reach: article.reach, // Ensure reach is included
        readingTime: article.readingTime, // Ensure readingTime is included
        date: article.date, // Ensure date is included
      }));
  } catch (error) {
    console.error('Error loading articles:', error);
    throw new Error('Failed to load articles');
  }
};

export const loadArticle = async (folder) => {
  try {
    const folderName = folder.split('/').pop();
    
    // Load metadata
    const meta = require(`../articles/${folderName}/meta.json`);
    
    // Load markdown content
    let content;
    try {
      content = require(`!!raw-loader!../articles/${folderName}/content.md`);
    } catch (e) {
      console.error('Error loading markdown:', e);
      content = '';
    }
    
    // Map author names to usernames
    const authorUsernames = meta.authors.map(name => {
      const author = authors.find(a => a.name === name);
      return author ? author.username : null;
    }).filter(username => username !== null);
    
    // Debug logging
    console.log('Folder:', folderName);
    console.log('Meta:', meta);
    console.log('Content:', content);
    
    // Create slugs for type and reach
    const typeSlug = slugify(meta.type);
    const reachSlug = slugify(meta.reach);
    
    return { 
      ...meta, 
      content: content.default || content,
      folder: `/articles/${folderName}`,
      authorUsernames, // Add usernames field
      typeSlug,        // Add slug for type
      reachSlug,       // Add slug for reach
      topics: meta.topics, // Use topics array
      reach: meta.reach, // Ensure reach is included
      readingTime: meta.readingTime, // Ensure readingTime is included
      date: meta.date, // Ensure date is included
    };
  } catch (error) {
    console.error(`Error loading article from ${folder}:`, error);
    throw new Error('Failed to load article');
  }
};