import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import { loadArticles } from '../utils/loadArticles';
import { Calendar, Clock } from 'lucide-react';
import { slugify } from '../utils/slugify'; // Import slugify utility

const Search = () => {
  const [articles, setArticles] = useState([]);
  const [query, setQuery] = useState('');
  const [filteredArticles, setFilteredArticles] = useState([]);
  const [types, setTypes] = useState([]);
  const [selectedType, setSelectedType] = useState('');
  
  // Updated state for filters
  const [startDate, setStartDate] = useState('');
  const [endDate, setEndDate] = useState('');
  const [targetReadingTime, setTargetReadingTime] = useState(15);
  
  useEffect(() => {
    const fetchArticles = async () => {
      try {
        const loadedArticles = await loadArticles();
        setArticles(loadedArticles);
        
        // Extract unique types
        const allTypes = loadedArticles.map(article => article.type);
        const uniqueTypes = [...new Set(allTypes)];
        setTypes(uniqueTypes);
      } catch (error) {
        console.error('Error loading articles:', error);
      }
    };
    fetchArticles();
  }, []);
  
  useEffect(() => {
    let results = articles;

    // Search query filter
    if (query) {
      results = results.filter(article => {
        const { title, subtitle, authors } = article;
        const searchString = `${title} ${subtitle} ${authors.join(' ')}`.toLowerCase();
        return searchString.includes(query.toLowerCase());
      });
    }

    // Type filter
    if (selectedType) {
      results = results.filter(article => article.type === selectedType);
    }

    // Date range filter
    if (startDate && endDate) {
      results = results.filter(article => {
        const articleDate = new Date(article.date);
        return articleDate >= new Date(startDate) && articleDate <= new Date(endDate);
      });
    }

    // Reading time proximity filter
    if (targetReadingTime) {
      results.sort((a, b) => {
        const aDiff = Math.abs(a.readingTime - targetReadingTime);
        const bDiff = Math.abs(b.readingTime - targetReadingTime);
        return aDiff - bDiff;
      });
    }

    setFilteredArticles(results);
  }, [query, selectedType, startDate, endDate, targetReadingTime, articles]);

  return (
    <div className="max-w-4xl mx-auto px-4 py-8">
      <h1 className="font-serif text-3xl mb-6">Search Articles</h1>
      
      {/* Search Input */}
      <input
        type="text"
        placeholder="Search by title, subtitle, or author"
        value={query}
        onChange={e => setQuery(e.target.value)}
        className="w-full border border-gray-300 rounded px-3 py-2 mb-6"
      />

      {/* Filter Panel */}
      <div className="space-y-6 mb-8">
        {/* Type Filter */}
        <div>
          <label className="block text-sm font-medium mb-2">Article Type</label>
          <select
            value={selectedType}
            onChange={e => setSelectedType(e.target.value)}
            className="w-full border border-gray-300 rounded px-3 py-2"
          >
            <option value="">All Types</option>
            {types.map((type, index) => (
              <option key={index} value={type}>{type}</option>
            ))}
          </select>
        </div>

        {/* Date Range */}
        <div>
          <label className="block text-sm font-medium mb-2">Date Range</label>
          <div className="grid grid-cols-2 gap-4">
            <input
              type="date"
              value={startDate}
              onChange={e => setStartDate(e.target.value)}
              className="border border-gray-300 rounded px-3 py-2"
            />
            <input
              type="date"
              value={endDate}
              onChange={e => setEndDate(e.target.value)}
              className="border border-gray-300 rounded px-3 py-2"
            />
          </div>
        </div>

        {/* Reading Time Slider */}
        <div>
          <label className="block text-sm font-medium mb-2">
            Target Reading Time: {targetReadingTime} minutes
          </label>
          <input
            type="range"
            min="1"
            max="60"
            value={targetReadingTime}
            onChange={e => setTargetReadingTime(Number(e.target.value))}
            className="w-full"
          />
          <div className="flex justify-between text-xs text-gray-500">
            <span>1 min</span>
            <span>60 min</span>
          </div>
        </div>
      </div>

      {/* Results Section */}
      {filteredArticles.length > 0 ? (
        <div className="grid grid-cols-1 gap-8">
          {filteredArticles.map((article, index) => (
            <Link
              key={index}
              to={`/article/${article.folder.split('/').pop()}`}
              className="block border-b pb-4"
            >
              <h2 className="font-serif text-2xl mb-1">{article.title}</h2>
              <p className="text-gray-600 mb-1">{article.subtitle}</p>
              <p className="text-sm text-gray-500">
                By {article.authors.join(', ')} on {article.date}
              </p>
              {article.type && (
                <span className="inline-block mt-2 px-2 py-1 bg-gray-200 text-xs rounded-full">
                  {article.type}
                </span>
              )}
            </Link>
          ))}
        </div>
      ) : (
        <p>No articles found.</p>
      )}
    </div>
  );
};

export default Search;
