// Consider renaming to TeamMember.js if authors are separate

import React from "react";
import { useParams } from "react-router-dom";
import { teamMembers } from "../data/teamMembers";
import noperson from "../noperson.jpg"; // Import the default image

const TeamMember = () => {
  const { username } = useParams();
  const member = teamMembers.find((m) => m.username === username);

  if (!member) {
    return <div>Person not found</div>;
  }

  return (
    <div className="max-w-4xl mx-auto p-4 flex flex-col md:flex-row">
      <img
        src={member.image}
        alt={member.name}
        className="w-48 h-48 rounded-full mb-4 md:mb-0 md:mr-6"
        onError={(e) => { e.target.src = "/images/team/noperson.jpg"; }} // Fallback to default image
      />
      <div>
        <h1 className="text-3xl font-bold">{member.name}</h1>
        <h2 className="text-xl text-gray-600 mb-4">{member.title}</h2>
        <p className="text-gray-800 leading-relaxed mb-4">{member.bio}</p>
        <p className="text-gray-700"><strong>Email:</strong> <a href={`mailto:${member.email}`} className="text-blue-500">{member.email}</a></p>
        <p className="text-gray-700"><strong>LinkedIn:</strong> <a href={member.linkedin} className="text-blue-500" target="_blank" rel="noopener noreferrer">View Profile</a></p>
      </div>
    </div>
  );
};

export default TeamMember;