// src/components/Layout.js
import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import { motion } from 'framer-motion';
import { loadArticles } from '../utils/loadArticles';
import { Linkedin, Instagram, Twitter } from 'lucide-react'; // Import lucide-react icons

const Layout = ({ children }) => {
  const today = new Date().toLocaleDateString('en-US', {
    weekday: 'long',
    month: 'long',
    day: 'numeric',
  });

  // Add state for categories
  const [categories, setCategories] = useState([]);

  // Fetch and set unique categories on component mount
  useEffect(() => {
    const fetchCategories = async () => {
      try {
        const articles = await loadArticles();
        const allTopics = articles.flatMap(article => article.topics); // Use flatMap for topics
        console.log('All topics:', allTopics);
        const filteredTopics = allTopics.filter(topic => topic !== undefined);
        const uniqueTopics = [...new Set(filteredTopics)];
        setCategories(uniqueTopics);
      } catch (error) {
        console.error('Error loading categories:', error);
      }
    };

    fetchCategories();
  }, []);

  return (
    <motion.div
      className="min-h-screen bg-white font-serif text-gray-900 overflow-x-hidden"
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      transition={{ duration: 0.5 }}
    >
      <div className="border-b text-xs bg-gray-100">
        <div className="container mx-auto px-4 py-2 flex justify-between">
          <div>{today}</div>
          <div className="flex gap-4">
            <Link to="/about" className="hover:underline">
              About
            </Link>
            <Link to="/search" className="hover:underline">
              Search
            </Link>
          </div>
        </div>
      </div>

      <header className="border-b">
        <div className="container mx-auto px-4 py-6">
          <div className="text-center">
            <Link to="/">
              <h1 className="font-lodeh xl:text-7xl lg:text-6xl md:text-5xl text-4xl tracking-wide leading-none">
                Youth Political Times
              </h1>
            </Link>
            <p className="text-sm italic mt-2">Your Trusted Source for Youth News</p>
          </div>
        </div>
      </header>

      <nav className="border-b">
        <div className="container mx-auto px-4">
          {/* Main Categories */}
          <ul className="flex justify-center space-x-8 py-3 text-sm font-medium uppercase tracking-wide">
            <li>
              <Link to="/topic/Domestic" className="hover:underline">
                Domestic
              </Link>
            </li>
            <li>
              <Link to="/topic/International" className="hover:underline">
                International
              </Link>
            </li>
            <li>
              <Link to="/topic/Local" className="hover:underline">
                Local
              </Link>
            </li>
          </ul>
          {/* Autopopulated Categories */}
          <ul className="flex justify-center space-x-6 py-2 text-xs text-gray-500">
            {categories.map((topic, index) => (
              <li key={index}>
                <Link to={`/topic/${topic}`} className="hover:underline">
                  {topic}
                </Link>
              </li>
            ))}
          </ul>
        </div>
      </nav>

      <main className="container mx-auto px-4 py-8">{children}</main>

      <footer className="bg-gray-50 border-t mt-12">
        <div className="container mx-auto px-4 py-12">
          <div className="grid grid-cols-1 md:grid-cols-3 gap-8">
            <div>
              <h3 className="font-bold mb-4 uppercase">Contact Us</h3>
              <ul className="space-y-2 text-sm">
                <li>Email: <a href="mailto:contact@youthpoliticaltimes.org" className="underline">contact@youthpoliticaltimes.org</a></li>
                <li className="flex space-x-4">
                  <a href="https://www.linkedin.com/company/youth-political-times" className="rounded-full p-2">
                    <Linkedin className="w-5 h-5" /> {/* LinkedIn Icon */}
                  </a>
                  <a href="https://www.instagram.com/youthpoliticaltimes/" className="rounded-full p-2">
                    <Instagram className="w-5 h-5" /> {/* Instagram Icon */}
                  </a>
                  <a href="https://twitter.com/youthpoliticaltimes" className="rounded-full p-2">
                    <Twitter className="w-5 h-5" /> {/* Twitter Icon */}
                  </a>
                </li>
              </ul>
            </div>
            
            <div>
              <h3 className="font-bold mb-4 uppercase">Links</h3>
              <ul className="space-y-2 text-sm">
                <li>
                  <Link to="/about" className="hover:underline">
                    About
                  </Link>
                </li>
                <li>
                  <Link to="/search" className="hover:underline">
                    Search
                  </Link>
                </li>
              </ul>
            </div>
            
            <div>
              <h3 className="font-bold mb-4 uppercase">Categories</h3>
              <ul className="space-y-2 text-sm">
                {categories.map((category, index) => (
                  <li key={index}>
                    <Link to={`/topic/${category}`} className="hover:underline">
                      {category}
                    </Link>
                  </li>
                ))}
              </ul>
            </div>
          </div>
          <div className="text-center text-xs text-gray-500 mt-8">
            &copy; {new Date().getFullYear()} Youth Political Times. All rights reserved.
          </div>
        </div>
      </footer>
    </motion.div>
  );
};

export default Layout;